interface DismissErrorButtonProps {
  onClick: () => void;
}

export const DismissErrorButton: React.FC<DismissErrorButtonProps> = ({
  onClick,
}) => {
  return (
    <button onClick={onClick} className="sdk-test-button-dismiss">
      Dismiss
    </button>
  );
};

