  // MARKET DATA ENDPOINTS (Public)
  // ============================================================================

  /**
   * Get all markets
   * Uses HTTP endpoint /market/v1/all (matches Go client GetMarkets)
   */
  async getMarkets(): Promise<any> {
    const response = await this.httpClient.get(
      "/market/v1/all",
      this.getHeaders(),
    );
    return response.body?.markets ?? response.body;
  }

  /**
   * Get markets with filters
   * Uses HTTP endpoint /market/v1/all (matches Go client GetMarketsWithFilters)
   */
  async getMarketsWithFilters(
    filter?: string,
    activeOnly?: boolean,
  ): Promise<any> {
    let path = "/market/v1/all";
    const params: string[] = [];
    if (filter) {
      params.push(`filter=${filter}`);
    }
    if (activeOnly) {
      params.push("active_only=true");
    }
    if (params.length > 0) {
      path += "?" + params.join("&");
    }
    const response = await this.httpClient.get(path, this.getHeaders());
    return response.body?.markets ?? response.body;
  }

