  /**
   * Get orderbook for a market
   * Uses HTTP endpoint /clob/v1/orderbook/{marketId} (matches Go client GetOrderbook)
   */
  async getOrderbook(
    marketId: number | string,
    convertToDecimal: boolean = true,
    depth: number = 20,
  ): Promise<any> {
    let path = `/clob/v1/orderbook/${marketId}`;
    let depthValue = 20;
    if (depth > 0 && depth < 100) {
      depthValue = depth;
    }
    path += `?depth=${depthValue}`;

    const response = await this.httpClient.get(path, this.getHeaders());

    if (convertToDecimal && response.body) {
      if (response.body.bids && Array.isArray(response.body.bids)) {
        response.body.bids = response.body.bids.map(
          ([price, size]: [string | number, string | number]) => [
            fromSatoshi(price),
            fromSatoshi(size),
          ],
        );
      }
      if (response.body.asks && Array.isArray(response.body.asks)) {
        response.body.asks = response.body.asks.map(
          ([price, size]: [string | number, string | number]) => [
            fromSatoshi(price),
            fromSatoshi(size),
          ],
        );
      }
    }

    return response.body;
  }

  /**
   * Get market trades by market ID
   * Uses HTTP endpoint /clob/v1/trades?market_id={marketId} (matches Go client GetMarketTrades)
   */
  async getMarketTrades(
    marketId: number | string,
    limit?: number,
  ): Promise<any> {
    let path = `/clob/v1/trades?market_id=${marketId}`;
    if (limit && limit > 0) {
      path += `&limit=${limit}`;
    }
    const response = await this.httpClient.get(path, this.getHeaders());
    console.debug(
      "[InfoClient] getMarketTrades response for marketId:",
      marketId,
      "response:",
      response.body,
    );

    // Normalize response to always return an array
    // Handle cases where API returns { trades: [...] }, { data: [...] }, or direct array
    if (Array.isArray(response.body)) {
      return response.body;
    }
    if (response.body?.trades && Array.isArray(response.body.trades)) {
      return response.body.trades;
    }
    if (response.body?.data && Array.isArray(response.body.data)) {
      return response.body.data;
    }

    console.warn(
      "[InfoClient] getMarketTrades: Unexpected response structure:",
      response.body,
    );
    // Return empty array if response structure is unexpected
    return [];
  }

