  /**
   * Get positions for authenticated user
   * Uses HTTP endpoint /bucket/v1/address/{address}/positions (matches Go client GetUserPositions)
   * Note: Requires walletAddress to be set or passed
   */
  async getPositions(
    walletAddress?: string,
    activeOnly?: boolean,
  ): Promise<any> {
    if (!walletAddress) {
      throw new Error("walletAddress is required for getPositions");
    }
    let path = `/bucket/v1/address/${walletAddress}/positions`;
    if (activeOnly !== undefined) {
      path += `?active_only=${activeOnly}`;
    }
    const response = await this.httpClient.get(path, this.getHeaders());
    // Normalize response to always return an array
    // Handle cases where API returns { positions: [...] } or direct array
    if (Array.isArray(response.body)) {
      return response.body;
    }
    if (response.body?.positions && Array.isArray(response.body.positions)) {
      return response.body.positions;
    }
    // Return empty array if response structure is unexpected
    return [];
  }

  /**
   * Get positions with decimal conversion for display
   */
  async getPositionsDecimal(
    walletAddress?: string,
    activeOnly?: boolean,
  ): Promise<any> {
    const positions = await this.getPositions(walletAddress, activeOnly);

    if (Array.isArray(positions)) {
      return positions.map((pos: any) => {
        // Handle both snake_case (from API) and camelCase field names
        const size = pos.size;
        const entryPrice = pos.entry_price || pos.entryPrice;
        const markPrice = pos.mark_price || pos.markPrice;
        const liquidationPrice = pos.liquidation_price || pos.liquidationPrice;
        const unrealizedPnL = pos.unrealized_pnl || pos.unrealizedPnL;

        return {
          ...pos,
          size: size ? fromSatoshi(size) : size,
          entry_price: entryPrice ? fromSatoshi(entryPrice) : entryPrice,
          entryPrice: entryPrice ? fromSatoshi(entryPrice) : entryPrice,
          mark_price: markPrice ? fromSatoshi(markPrice) : markPrice,
          markPrice: markPrice ? fromSatoshi(markPrice) : markPrice,
          liquidation_price: liquidationPrice
            ? fromSatoshi(liquidationPrice)
            : liquidationPrice,
          liquidationPrice: liquidationPrice
            ? fromSatoshi(liquidationPrice)
            : liquidationPrice,
          unrealized_pnl: unrealizedPnL
            ? fromSatoshi(unrealizedPnL)
            : unrealizedPnL,
          unrealizedPnL: unrealizedPnL
            ? fromSatoshi(unrealizedPnL)
            : unrealizedPnL,
        };
      });
    }

    return positions;
  }

