  /**
   * Get balance with decimal conversion
   */
  async getBalanceDecimal(address: string, assetID: string): Promise<any> {
    const balance = await this.getBalance(address, assetID);

    if (balance && typeof balance === "object") {
      const converted: Record<string, any> = { ...balance };
      if (converted.balance) {
        converted.balance = fromSatoshi(converted.balance);
      }
      if (converted.available) {
        converted.available = fromSatoshi(converted.available);
      }
      if (converted.locked) {
        converted.locked = fromSatoshi(converted.locked);
      }
      return converted;
    }

    return balance;
  }

  /**
   * Get orders for an address
   * Uses HTTP endpoint /clob/v1/address/orders (matches Go client GetOrders)
   * @param address - Wallet address to get orders for
   * @param assetPair - Optional asset pair ID to filter orders
   * @returns Response object with structure: { orders: Order[], total_count: number, timestamp: string }
   * @example
   * // Get all orders for an address
   * const result = await client.getOrders('0x3061750d3dF69ef7B8d4407CB7f3F879Fd9d2398');
   * // result.orders contains the array of orders
   * // result.total_count contains the total count
   *
   * // Get orders filtered by asset pair
   * const result = await client.getOrders('0x3061750d3dF69ef7B8d4407CB7f3F879Fd9d2398', '16210999351379558260');
   */
  async getOrders(address: string, assetPair?: string): Promise<any> {
    let path = `/clob/v1/address/orders?address=${encodeURIComponent(address)}`;
    if (assetPair) {
      path += `&asset_pair=${encodeURIComponent(assetPair)}`;
    }
    const response = await this.httpClient.get(path, this.getHeaders());
    return response.body;
  }

