  /**
   * Subscribe to ticker updates (moniker channel)
   * Note: Router uses "moniker" channel name, but we keep "ticker" in the API for consistency
   */
  subscribeTicker(
    marketId: number | string,
    callback: SubscriptionCallback,
  ): void {
    if (!this.isConnected()) {
      throw new Error("WebSocket not connected. Call connect() first.");
    }

    // Router protocol uses "moniker" channel name, but we track as "ticker" internally
    const routerChannel = "moniker";
    const internalChannel = "ticker";
    this.subscriptions.set(internalChannel, [
      ...(this.subscriptions.get(internalChannel) || []),
      callback,
    ]);

    // Wrap callback to handle Router protocol format
    // WebSocketClient now routes type="data" messages by channel name, so we can register directly on channel
    const wrappedCallback = (message: any) => {
      const tickerData = this.extractRouterData(message);
      if (tickerData) {
        callback(tickerData);
      }
    };

    // Store wrapped callback for proper cleanup
    if (!this.wrappedCallbacks.has(internalChannel)) {
      this.wrappedCallbacks.set(internalChannel, new Map());
    }
    this.wrappedCallbacks.get(internalChannel)!.set(callback, wrappedCallback);

    // Register handler on router channel name (WebSocketClient routes type="data" messages by channel)
    this.wsClient.on(routerChannel, wrappedCallback);

    // Send subscription message using Router protocol (with "moniker" channel name)
    try {
      this.wsClient.send({
        type: "subscribe",
        channel: routerChannel,
        params: {
          market_id: marketId.toString(),
        },
      });
    } catch (error) {
      // Clean up on send failure
      this.wrappedCallbacks.get(internalChannel)?.delete(callback);
      const callbacks = this.subscriptions.get(internalChannel) || [];
      const index = callbacks.indexOf(callback);
      if (index > -1) {
        callbacks.splice(index, 1);
        this.subscriptions.set(internalChannel, callbacks);
      }
      throw error;
    }
  }

  /**
   * Unsubscribe from ticker updates
   */
  unsubscribeTicker(
    marketId: number | string,
    callback?: SubscriptionCallback,
  ): void {
    const internalChannel = "ticker";
    const routerChannel = "moniker";
    const callbacks = this.subscriptions.get(internalChannel) || [];

    if (callback) {
      const index = callbacks.indexOf(callback);
      if (index > -1) {
        callbacks.splice(index, 1);
        this.subscriptions.set(internalChannel, callbacks);

        // Remove wrapped callback
        const wrappedCallback = this.wrappedCallbacks
          .get(internalChannel)
          ?.get(callback);
        if (wrappedCallback) {
          this.wsClient.off(routerChannel, wrappedCallback);
          this.wrappedCallbacks.get(internalChannel)!.delete(callback);
        }
      }
    } else {
      // Remove all callbacks
      const channelWrappedCallbacks =
        this.wrappedCallbacks.get(internalChannel);
      if (channelWrappedCallbacks) {
        channelWrappedCallbacks.forEach((wrappedCallback) => {
          this.wsClient.off(routerChannel, wrappedCallback);
        });
        this.wrappedCallbacks.delete(internalChannel);
      }
      this.subscriptions.delete(internalChannel);
    }

    if (
      this.subscriptions.get(internalChannel)?.length === 0 &&
      this.isConnected()
    ) {
      try {
        this.wsClient.send({
          type: "unsubscribe",
          channel: routerChannel,
          params: {
            market_id: marketId.toString(),
          },
        });
      } catch (error) {
        console.error("Failed to send unsubscribe message:", error);
      }
    }
  }

