  /**
   * Subscribe to last price updates
   * @param marketId - Market ID to subscribe to (string or number)
   * @param callback - Callback function for last price updates
   * @param convertToDecimal - Whether to convert satoshi to decimal (default: true)
   */
  subscribeLastPrice(
    marketId: number | string,
    callback: SubscriptionCallback,
    convertToDecimal: boolean = true,
  ): void {
    if (!this.isConnected()) {
      throw new Error("WebSocket not connected. Call connect() first.");
    }

    const channel = "last_price";
    this.subscriptions.set(channel, [
      ...(this.subscriptions.get(channel) || []),
      callback,
    ]);

    // Wrap callback to handle decimal conversion and Router protocol format
    // WebSocketClient now routes type="data" messages by channel name, so we can register directly on channel
    const wrappedCallback = (message: any) => {
      console.debug("[SubscriptionClient] last_price message received:", message);
      const tradeData = this.extractRouterData(message);
      if (!tradeData) {
        console.warn("[SubscriptionClient] Failed to extract trade data from message:", message);
        return;
      }

      console.debug("[SubscriptionClient] Extracted trade data:", tradeData);

      // Validate required fields
      if (!tradeData.price) {
        console.warn("[SubscriptionClient] Trade data missing price field:", tradeData);
        return;
      }

      // Convert price and quantity from satoshi strings to decimal numbers
      if (convertToDecimal) {
        if (tradeData.price) {
          tradeData.price = fromSatoshi(tradeData.price);
        }
        if (tradeData.quantity) {
          tradeData.quantity = fromSatoshi(tradeData.quantity);
        }
      }

      console.debug("[SubscriptionClient] Calling callback with trade data:", tradeData);
      callback(tradeData);
    };

    // Store wrapped callback for proper cleanup
    this.lastPriceWrappedCallbacks.set(callback, wrappedCallback);

    // Register handler on channel name (WebSocketClient routes type="data" messages by channel)
    this.wsClient.on(channel, wrappedCallback);

    // Send subscription message
    try {
      this.wsClient.send({
        type: "subscribe",
        channel: "last_price",
        params: {
          market_id: marketId.toString(),
        },
      });
    } catch (error) {
      // Clean up on send failure
      this.lastPriceWrappedCallbacks.delete(callback);
      const callbacks = this.subscriptions.get(channel) || [];
      const index = callbacks.indexOf(callback);
      if (index > -1) {
        callbacks.splice(index, 1);
        this.subscriptions.set(channel, callbacks);
      }
      throw error;
    }
  }

  /**
   * Unsubscribe from last price updates
   */
  unsubscribeLastPrice(
    marketId: number | string,
    callback?: SubscriptionCallback,
  ): void {
    const channel = "last_price";
    const callbacks = this.subscriptions.get(channel) || [];

    if (callback) {
      const index = callbacks.indexOf(callback);
      if (index > -1) {
        callbacks.splice(index, 1);
        this.subscriptions.set(channel, callbacks);

        // Remove wrapped callback
        const wrappedCallback = this.lastPriceWrappedCallbacks.get(callback);
        if (wrappedCallback) {
          this.wsClient.off(channel, wrappedCallback);
          this.lastPriceWrappedCallbacks.delete(callback);
        }
      }
    } else {
      // Remove all callbacks
      callbacks.forEach((cb) => {
        const wrappedCallback = this.lastPriceWrappedCallbacks.get(cb);
        if (wrappedCallback) {
          this.wsClient.off(channel, wrappedCallback);
          this.lastPriceWrappedCallbacks.delete(cb);
        }
      });
      this.subscriptions.delete(channel);
    }

    // Send unsubscribe message if no more callbacks and connected
    if (this.subscriptions.get(channel)?.length === 0 && this.isConnected()) {
      try {
        this.wsClient.send({
          type: "unsubscribe",
          channel: "last_price",
          params: {
            market_id: marketId.toString(),
          },
        });
      } catch (error) {
        console.error("Failed to send unsubscribe message:", error);
      }
    }
  }

