  async bucketTransfer(params: BucketTransferParams): Promise<any> {
    // Input validation
    if (!params.fromBucketId || params.fromBucketId.trim() === '') {
      throw new Error('fromBucketId cannot be empty');
    }
    if (!params.toBucketId || params.toBucketId.trim() === '') {
      throw new Error('toBucketId cannot be empty');
    }
    if (!params.assetId || params.assetId.trim() === '') {
      throw new Error('assetId cannot be empty');
    }
    if (params.amount === undefined || (typeof params.amount === 'number' && params.amount <= 0)) {
      throw new Error('amount must be positive');
    }

    if (!this.walletAddress) {
      await this.initialize();
    }

    const walletAddress = this.getWalletAddress();
    const amountStr = typeof params.amount === 'string'
      ? params.amount
      : toSatoshi(params.amount);

    // Build payload for EIP712 signature (matches Go test payload structure)
    // Payload uses: owner, fromBucketId, toBucketId, asset, amount, nonce, deadline
    // Note: owner, nonce, and deadline are added by buildCompleteMessage
    const payload: Record<string, any> = {
      action: OpBankBucketTransfer,
      fromBucketId: params.fromBucketId,
      toBucketId: params.toBucketId,
      asset: params.assetId, // Use "asset" not "assetId" in payload
      amount: amountStr,
    };

    const completeMessage = await this.buildCompleteMessage(payload);
    const signature = await this.signPayload(completeMessage);

    // Build flat HTTP request structure (matches Go test reqBody structure)
    // HTTP request uses: address, source_bucket_id, target_bucket_id, amount, reason, timestamp, nonce, deadline, signature, chain_id
    const timestamp = Math.floor(Date.now() / 1000);
    const reqBody: Record<string, any> = {
      address: walletAddress,
      source_bucket_id: params.fromBucketId, // snake_case in HTTP body
      target_bucket_id: params.toBucketId, // snake_case in HTTP body
      amount: amountStr,
      reason: "sdk_transfer", // Optional field, present in Go test
      timestamp: timestamp,
      nonce: completeMessage.nonce,
      deadline: completeMessage.deadline,
      signature: signature,
      chain_id: this.chainID.toString(),
    };

    // Submit to /bucket/v1/bucket/transfer endpoint with flat structure
    const response = await this.httpClient.post('/bucket/v1/bucket/transfer', reqBody);
    return response.body;
  }

